import React, { useEffect, useState } from 'react';

// styles
import {
  Container,
  ModalHeader,
  ModalContent,
  Overlay,
  Divider,
} from './styles';

const Modal = ({ isOpen, companyFee, percentage }) => {
  const [isModalOpen, setIsOpen] = useState(true);
  const [restitutionValue, setRestitutionValue] = useState(0);
  const [monthlyValue, setMonthlyValue] = useState(0);

  useEffect(() => {
    setIsOpen(isOpen);
  }, [isOpen]);

  useEffect(() => {
    let i = 0;
    const anualFee = parseFloat(companyFee) * 12;

    if (anualFee <= 180000) {
      i = 0;
    } else if (anualFee > 180000 && anualFee <= 360000) {
      i = 1;
    } else if (anualFee > 360000 && anualFee <= 720000) {
      i = 2;
    } else if (anualFee > 720000 && anualFee <= 1800000) {
      i = 3;
    } else if (anualFee > 1800000 && anualFee <= 3600000) {
      i = 4;
    } else if (anualFee > 3600000 && anualFee < 4800000) {
      i = 5;
    } else {
      i = 6;
    }

    const AliquotaFaixa = [];
    const pisFaixa = [];
    const cofinsFaixa = [];
    const valorDeduzirFaixa = [];

    //Aliquota em %
    AliquotaFaixa[0] = 0.04;
    AliquotaFaixa[1] = 0.073;
    AliquotaFaixa[2] = 0.095;
    AliquotaFaixa[3] = 0.107;
    AliquotaFaixa[4] = 0.143;
    AliquotaFaixa[5] = 0.19;

    //PIS/PASEP em %
    if (i !== 6) {
      pisFaixa[i] = 0.0276;
    } else {
      pisFaixa[i] = 0.0613;
    }

    // Cofins em %
    if (i !== 6) {
      cofinsFaixa[i] = 0.1274;
    } else {
      cofinsFaixa[i] = 0.2827;
    }

    //valor a deduzir em R$
    valorDeduzirFaixa[0] = 0;
    valorDeduzirFaixa[1] = 5940;
    valorDeduzirFaixa[2] = 13860;
    valorDeduzirFaixa[3] = 22500;
    valorDeduzirFaixa[4] = 87300;
    valorDeduzirFaixa[5] = 378000;

    let aliquotaEfetiva = 0;
    //ALÍQUOTA EFETIVA
    aliquotaEfetiva =
      (anualFee * AliquotaFaixa[i] - valorDeduzirFaixa[i]) / anualFee;
    aliquotaEfetiva = parseFloat(aliquotaEfetiva.toFixed(6));

    //CÁLCULO DO SIMPLES NACIONAL
    const calculo2 = companyFee * aliquotaEfetiva;
    //Seguimento - 100%
    let aux = 1 - percentage;
    aux = parseFloat(aux).toFixed(2);

    //BASE DE aliquotaEfetiva (seguimento) TRIBUTADO
    const tributado = companyFee * aux * aliquotaEfetiva;

    //Porcentagem do MONOFASICO
    const somaGerais = cofinsFaixa[i] + pisFaixa[i];
    const multiGerais = aliquotaEfetiva * somaGerais;
    let monofasico = aliquotaEfetiva - multiGerais;
    monofasico = parseFloat(monofasico).toFixed(4);

    //BASE DE aliquotaEfetiva 80% MONOFASICO
    const monofasicoFinal = companyFee * percentage * monofasico;

    //TOTAL RECALCULADO
    const totalRecalculado = tributado + monofasicoFinal;

    //ECONOMIA MENSAL
    const economiaMensal = calculo2 - totalRecalculado;

    //VALOR RESTITUIDO
    const restituido = economiaMensal * 60;

    setRestitutionValue(economiaMensal);
    setMonthlyValue(restituido);
  }, [companyFee, percentage]);

  return (
    <Overlay
      onClick={() => setIsOpen(false)}
      style={{ display: isModalOpen ? 'inherit' : 'none' }}
    >
      <Container>
        <ModalHeader>
          <p>X</p>
        </ModalHeader>
        <Divider />
        <ModalContent>
          <p>Economia mensal: R$ {monthlyValue.toFixed(2)}</p>

          <p>Seu valor a ser restituído é: R$ {restitutionValue.toFixed(2)}</p>
        </ModalContent>
      </Container>
    </Overlay>
  );
};

export default Modal;
