import React, { useCallback, useState } from 'react';

// styles
import {
  Container,
  Input,
  InputLabel,
  InputWrapper,
  Wrapper,
  Select,
  Button,
  InputIcon,
} from './styles';
import Modal from '../Modal';

const Calculator = () => {
  const [currentOption, setCurrentOption] = useState(0.7);
  const [companyFee, setCompanyFee] = useState(0);

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenLink = useCallback(() => {
    const url =
      'https://api.whatsapp.com/send?phone=5532991215205&text=Olá!%20Quero%20restituir%20os%20impostos%20da%20minha%20empresa.';
    window.open(url, '_blank').focus();
  }, []);

  const handleOpenModal = useCallback(() => {
    setIsModalOpen(true);
  }, []);

  const handleSelectOption = useCallback(option => {
    setCurrentOption(option);
  }, []);

  return (
    <Container>
      <Wrapper>
        <InputLabel>INFORME SEU FATURAMENTO MENSAL</InputLabel>
        <InputWrapper>
          <InputIcon>R$</InputIcon>
          <Input
            value={companyFee}
            onChange={e => setCompanyFee(e.target.value)}
            type="number"
            min="1"
            step="0.01"
          />
        </InputWrapper>
      </Wrapper>
      <Wrapper>
        <InputLabel>QUAL É O SEU SEGMENTO?</InputLabel>
        <Select onChange={e => handleSelectOption(e.target.value)}>
          <option value="0.70">Adega</option>
          <option value="0.80">Autopeças</option>
          <option value="0.25">Bares/Restaurantes</option>
          <option value="0.25">Casa de show</option>
          <option value="0.50">Centro automotivo</option>
          <option value="0.90">Farmacias</option>
          <option value="0.70">Loja de conveniencia</option>
          <option value="0.10">Mercados/Mercearias</option>
          <option value="0.85">Perfumaria</option>
          <option value="0.30">Petshop</option>
        </Select>
      </Wrapper>
      tax
      <Wrapper>
        <Button onClick={handleOpenModal}>CALCULAR SEU CRÉDITO!</Button>
        <Button onClick={handleOpenLink}>QUERO RESTITUIR AGORA!</Button>
      </Wrapper>
      <Modal
        isOpen={isModalOpen}
        companyFee={companyFee}
        percentage={currentOption}
      />
    </Container>
  );
};

export default Calculator;
